body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.flex-wrapper {
  background: url("https://media.giphy.com/media/3oz8xBvY5MyxIdyuxa/source.gif") no-repeat center fixed;
  background-size: cover;
  display: flex;
  justify-content: center;
  height: 100vh;
  flex-direction: column;
  text-align: center; }
  .flex-wrapper .StartPageHeader {
    color: white;
    margin: auto auto 0; }

.welcomeForm {
  height: 50px;
  width: 200px;
  color: white;
  margin: 0 auto auto; }
  .welcomeForm .nameLabel {
    color: #fff; }
    .welcomeForm .nameLabel label, .welcomeForm .nameLabel input {
      color: #fff; }
  .welcomeForm .jumpInButton {
    margin-top: 20px;
    width: 200px; }

